﻿@import url('https://fonts.googleapis.com/css?family=PT+Sans');


* {
    padding: 0;
    margin: 0;
    color: slategray;
    font-family: 'PT Sans', sans-serif;
}

.grid{
    display: grid;
}

input, textarea {
    border: none;
    border-bottom: 3px solid slategray;
    font-size: 18px;
}
button, input {
    background: none;
    border: none;
}
a{
    text-decoration: none;
}
li{
    list-style: none;
}
img{
    display: block;
    width: 100%;
}